@import url(https://fonts.googleapis.com/css?family=Fira+Sans:200,400&display=swap);
.photo{display:flex;justify-content:center;align-items:center;height:50%;background:#073b4c;background-size:cover;background-repeat:no-repeat;background-position:center;color:#111;border:5px solid;outline:5px #111}@media only screen and (min-device-width: 1024px){.photo{width:50%;outline:none}}.photo:first-child,.photo:nth-child(-n+2){border-left:10px solid}.photo:last-child,.photo:nth-last-child(2){border-right:10px solid}.photo:nth-child(6n-5),.photo:nth-child(6n-3),.photo:nth-child(6n-1){border-top:10px solid}.photo:nth-child(6n-4),.photo:nth-child(6n-2),.photo:nth-child(6n){border-bottom:10px solid}.photo:nth-child(6n-5){width:25%}@media only screen and (min-device-width: 1024px){.photo:nth-child(6n-5){height:25%;width:auto;width:initial}}.photo:nth-child(6n-4){width:75%}@media only screen and (min-device-width: 1024px){.photo:nth-child(6n-4){height:75%;width:auto;width:initial}}.photo:nth-child(6n-3),.photo:nth-child(6n-2){width:50%}@media only screen and (min-device-width: 1024px){.photo:nth-child(6n-3),.photo:nth-child(6n-2){height:50%;width:auto;width:initial}}.photo:nth-child(6n-1){width:70%}@media only screen and (min-device-width: 1024px){.photo:nth-child(6n-1){height:70%;width:auto;width:initial}}.photo:nth-child(6n){width:30%}@media only screen and (min-device-width: 1024px){.photo:nth-child(6n){height:30%;width:auto;width:initial}}

